import {
  START_GAME,
  MOVE_DOWN,
  MOVE_RIGHT,
  MOVE_LEFT,
  ROTATE
} from '../constants/actionTypes';

import {
  getInitialState,
  startGame,
  moveDown,
  moveLeft,
  moveRight,
  rotate
} from './selectors';

export default function game(state = getInitialState(), action) {
  switch (action.type) {
    case START_GAME:
      return startGame(state, action);
    case MOVE_DOWN:
      return moveDown(state, action);
    case ROTATE:
        return rotate(state, action);
    case MOVE_LEFT:
        return moveLeft(state, action);
    case MOVE_RIGHT:
        return moveRight(state, action);
    default:
      return state;
  }
}



