const Tetrominos = {
  O: {
    shape: [
      [1, 1],
      [1, 1]
    ],
    color: '#EC5f67'
  },
  I: {
    shape: [
      [1, 1, 1, 1]
    ],
    color: '#99C794'
  },
  J: {
    shape: [
      [1, 0, 0],
      [1, 1, 1]
    ],
    color: '#C594C5'
  },
  L: {
    shape: [
      [0, 0, 1],
      [1, 1, 1]
    ],
    color: '#6699CC'
  },
  S: {
    shape: [
      [0, 1, 1],
      [1, 1, 0]
    ],
    color: '#5FB3B3'
  },
  T: {
    shape: [
      [0, 1, 0],
      [1, 1, 1]
    ],
    color: '#FAC863'
  },
  Z: {
    shape: [
      [1, 1, 0],
      [0, 1, 1]
    ],
    color: '#F99157'
  }
};

export default Tetrominos;



